import React from 'react';
import {Rect, Layer} from 'react-konva';

class GameGrid extends React.Component {
    constructor(props) {
          super(props);
    }

    render() {
        let squares = [];
        const rowsCols = this.props.size;
        const numberSquares = rowsCols * rowsCols;
        for (var i = 0; i < numberSquares; i++) {
            let defaultSize = 10;
            let size = (this.props.isPlayer) ? defaultSize * 3 : defaultSize * 11;
            let x = size * (i % rowsCols);
            let y = size * (Math.floor(i / rowsCols));
            squares.push(<Rect key={i} x={x} y={y} width={100} height={100} fill={'darkolivegreen'} />);
        }

        return (
            <Layer>
                { squares }
            </Layer>
        );
    }
}

export default GameGrid;



